#fullsize img {
    background: #fff;
}

.zoom-image {
    width: 90%;
    height: 350px;
    overflow: hidden;
    position: absolute;
    border: 1px solid #e9ecef;
}

.zoom-image img {
    position: absolute;
    height: auto;
}

.zoom-controls {
    position: absolute;
    top: 0px;
    right: 0px;
    z-index: 5;
}

.zoom-controls .zoom {
    display: inline-block;
    font-size: 17px;
    font-weight: bold;
    margin: 3px 1px;
    padding: 0px 17px;
    border-radius: 4px;
    text-decoration: none;
}

.zoom-in {
    position: absolute;
    top: 0;
}

.zoom-out {
    position: absolute;
    bottom: -25px;
    right: 2px;
}

/* .zoom-controls .zoom-reset { */
/* font-size: 14px; */
/* } */

.navigator {
    position: relative;
    top: 147px;
    width: 100%;
}

.navigator button.prev {
    position: absolute;
    left: 0px;
}

.navigator button.next {
    position: absolute;
    right: 0px;
}

.save-btn {
    box-shadow: 0 0 6px 1px rgba(255, 100, 0, 0.29) !important;
    background-color: #ff6400;
}

mat-slider {
    height: 300px;
    margin-top: 25px;
}

.title {
    word-break: break-all;
    font-size: 16px;
    margin: 0px 30px !important;
}

@media screen and (max-width:500px) {
    .title {
        font-size: 14px;
    }
}

@media screen and (max-width:700px) {
    .zoom-image {
        width: 80%;
    }
}

@media screen and (max-width:900px) {
    .title {
        line-height: 1.2 !important;
    }
}